const publicPropertiesMap = {
    //  instance.proxy.$emit 
    $emit: (i) => i.emit,
};

export const PublicInstanceProxyHandlers = {
    get({ _: instance }, key) {
        console.log(" proxy  hook");
        const publicGetter = publicPropertiesMap[key];

        if (publicGetter) {
            return publicGetter(instance);
        }
    },
};
